import { setMessage } from '../store/actions/MessageActions';
import { http } from '../utils/http';

export const calculatePlan = (payload, dispatch, setMessage) => {
    return new Promise((resolve, reject) => {
        http(process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS, 'POST', 'subscriptions-service/plan-subscriptions/calculate', payload, true)
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
                dispatch(setMessage(err?.data?.message ? err?.data?.message : 'Already Subscribed!!'));
            });
    });
};
